@import "main-bx";

.impact {
    &__inner {
        margin-top: 28px;
        position: relative;
    }

    &__img {
        width: 100%;
        max-width: 636px;
        height: 636px;
        margin: 0 auto;

        @include below(768px) {
            max-width: 307px;
            height: 307px;
        }
    }

    &__over {
        position: absolute;
        top: 25px;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 10;
        display: flex;
        justify-content: space-between;

        @include below(768px) {
            position: relative;
            flex-direction: column;
        }
    }


    &__list {
        width: 28%;
        @include below(768px) {
            width: 100%;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: -8px;
        }
        &_reverse {
            flex-direction: row-reverse;
            text-align: right;

            @include below(768px) {
                flex-direction: row;
                text-align: left;
            }
            img {
                margin-left: 5px;
                margin-right: 0;
            }
        }

        span {
            @include fontProperty(700, 22px, 110%, $color_dark);

            @include below(768px) {
                font-size: 22px;
                line-height: 110%;
                br {
                    display: none;
                }
            }
        }

        img {
            width: 100px;
            height: 100px;
            display: block;
            margin-right: 5px;

            @include below(768px) {
                width: 80px;
                height: 80px;
                margin: 0;
            }
        }

        &_lf2 {
            margin-right: 50px;
            @include below(768px) {
                margin-right: 0;
            }
        }

        &_lf3 {
            margin-right: 70px;
            @include below(768px) {
                margin-right: 0;
            }
        }

        &_rg2 {
            margin-left: 50px;
            @include below(768px) {
                margin-left: 0;
            }
        }

        &_rg3 {
            margin-left: 70px;
            @include below(768px) {
                margin-left: 0;
            }
        }

        &_rg4 {
            margin-left: -50px;
            @include below(768px) {
                margin-left: 0;
            }
        }
    }

    
}