@import "main-bx";

.intro {
    @include below(768px) {
        background-image: url(../img/content/m1.jpg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        margin-bottom: 20px;
    }
    &__inner {
        position: relative;
        margin-top: 40px;
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(131, 139, 149, 0.5);

        @include below(768px) {
            margin-top: 0;
            border: 0;
        }
    }

    &__img {
        img {
            display: block;
            border-radius: 10px;
        }

        @include below(900px) {
            height: 375px;
        }

        @include below(768px) {
            display: none;
        }
    }

    &__content {
        position: absolute;
        top: 100px;
        left: 80px;

        width: 100%;
        max-width: 600px;

        @include below(768px) {
            position: relative;
            max-width: 100%;
            top: auto;
            left: auto;
        }
    }

    &__title {
        @include fontProperty(800, 48px, 120%, $color_dark);
        margin-bottom: 50px;

        @include below(1320px) {
            font-size: 36px;
        }

        @include below(768px) {
            font-size: 24px;
            line-height: 120%;
            text-align: center;
            padding-top: 40px;
            margin-bottom: 40px;
        }
    }

    &__btn {
        
        a {
            display: inline-flex;
            align-items: center;
            padding: 15px 30px 15px 15px;
        }

        svg {
            width: 20px;
            height: 20px;
            margin-right: 15px;

            @include below(768px) {
                width: 50px;
                height: 50px;
                margin-right: 0;
            }
        }

        @include below(768px) {
            display: flex;
            justify-content: center;
            span {
                display: none;
            }

            .btn {
                padding: 0;
                background-color: transparent;
            }
        }
    }
}