@import "main-bx";

.payment {
    position: relative;

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }
    &__inner {
        position: relative;
        z-index: 2;
        padding-top: 60px;
        padding-bottom: 60px;

        @include below(768px) {
            padding-top: 20px;
            padding-bottom: 30px;
        }
    }

    &__title {
        @include fontProperty(700, 30px, 37px, $white);
        margin-bottom: 32px;

        @include below(768px) {
            font-size: 23px;
            line-height: 28px;
            margin-bottom: 20px;
        }
    }

    &__form {
        width: 100%;
        max-width: 730px;
    }

    &__form-row {
        display: flex;
        justify-content: space-between;
        &:not(:last-child) {
            margin-bottom: 20px;
            @include below(768px) {
                margin-bottom: 0;
            }
        }

        @include below(768px) {
            flex-direction: column;
        }
    }

    &__form-input {
        width: 31.5%;
        @include below(768px) {
            width: 100%;
            margin-bottom: 16px;
        }
    }

    &__form-message {
        width: 66%;
        @include below(768px) {
            width: 100%;
            margin-bottom: 16px;
        }
    }

    &__form-right {
        width: 31.5%;
        @include below(768px) {
            width: 100%;
            margin-bottom: 14px;
        }
    }

    &__form-policy {
        @include fontProperty(400, 12px, 140%);
        color: #F3F4F4;
        width: 55%;
        a {
            color: #F3F4F4;
            border-bottom: 1px solid #F3F4F4;
            
        }

        @include below(768px) {
            width: 100%;
            order: 2;
        }
    }

    &__form-status {
        @include fontProperty(400, 14px);
        color: #F3F4F4;
        display: flex;
        align-items: center;
        &::before {
            content: "";
            display: block;
            width: 10px;
            height: 10px;
            background-color: #29C555;
            border-radius: 50%;
            margin-right: 6px;
        }

        @include below(768px) {
            order: 1;
        }
    }
}