@import "main-bx";

.menu {
    &__list {
        display: flex;
        align-items: center;
    }

    &__list-item {
        &:not(:last-child) {
            margin-right: 40px;
        }
        a {
            display: block;
            padding: 14px 0;

            @include fontProperty(800, 16px, 22px, $white);
            letter-spacing: 0.05em;
            text-transform: uppercase;
        }
    }
}