@import "main-bx";

.special {
    &__inner {
        display: flex;
        justify-content: space-between;
        
        padding-top: 30px;
        padding-bottom: 100px;

        @include below(1320px) {
            padding-bottom: 40px;
        }

        @include below(1024px) {
            flex-wrap: wrap;
        }

        @include below(768px) {
            // border-top: 1px solid rgba(131, 139, 149, 0.5);
            padding-top: 20px;
            padding-bottom: 60px;
        }
    }

    &__item {
        position: relative;
        width: 100%;
        max-width: 360px;
        @include below(768px) {
            padding-left: 95px;
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
    }

    &__head {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        @include below(768px) {
            margin-bottom: 10px;
        }
    }

    &__icon {
        width: 100px;
        height: 100px;
        margin-right: 30px;

        @include below(768px) {
            position: absolute;
            top: 0;
            left: 0;
            width: 75px;
            height: 75px;
            margin-right: 0;
        }
    }

    &__title {
        @include fontProperty(700, 22px, 110%, $color_dark);
        @include below(768px) {
            font-size: 18px;
            line-height: 110%;
        }
    }

    &__text {
        @include fontProperty(400, 18px, 120%, $color_dark);
        text-align: center;

        @include below(768px) {
            text-align: left;
            font-size: 14px;
            line-height: 120%;
        }
    }
    &__link {
        display: block;
        text-align: center;
        @include below(768px) {
            display: inline-block;
            text-align: left;
            font-size: 14px;
        }
    }
}