@import "main-bx";

.price {
    &__inner {
        padding-top: 50px;
        display: flex;
        justify-content: space-between;
        @include below(1130px) {
            flex-wrap: wrap;
        }
    }

    &__img {
        @include below(768px) {
            width: 243px;
            height: 170px;
            margin: 0 auto;
        }
    }

    &__item {
        border: 1px solid #F3F4F4;
        border-radius: 10px;
        overflow: hidden;
        transition: all .2s linear;
        width: 24%;
        &:hover {
            box-shadow: 0px 10px 20px rgba(56, 63, 68, 0.1);
            .price__cost-num {
                color: $color_green;
            }
        }

        @include below(1130px) {
            width: 47%;
            margin-bottom: 20px;
        }

        @include below(768px) {
            width: 100%;
        }
    }

    &__center {
        padding: 12px 20px 24px;
    }

    &__title {
        @include fontProperty(800, 20px, 100%, $color_dark);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    &__noise {
        display: flex;
        align-items: center;
        @include fontProperty(400, 16px, 120%, $color_peru);
        margin-bottom: 12px;
        img {
            margin-right: 6px;
            width: 20px;
            height: 20px;
        }
    }

    &__desc {
        padding-bottom: 10px;
        border-bottom: 1px solid #F3F4F4;
        margin-bottom: 20px;
        dd {
            padding: 0;
            margin: 0;
            display: flex;
            align-items: center;
            margin-bottom: 10px;
        }
    }

    &__key {
        @include fontProperty(400, 14px, 120%, rgba(131, 139, 149, 0.7));
        margin-right: 6px;
    }

    &__val {
        @include fontProperty(600, 14px, 120%, $color_slates);
    }

    &__cost {
        display: flex;
        align-items: center;
        
        &-text {
            @include fontProperty(400, 24px, 100%, $color_slates);
            margin-right: 10px;
            opacity: 0.5;
        }

        &-num {
            @include fontProperty(800, 24px, 100%, $color_dark);
            transition: all .2s linear;
        }
    }

    &__btn {
        button {
            width: 100%;
            display: flex;
            justify-content: center;
            padding: 15px 0;
            border-top-left-radius: 0;
            border-top-right-radius: 0;

            &:hover,
            &:active {
                border-radius: 10px;
            }
        }
    }
}