@import "main-bx";

.reply {
    &__inner {
        margin-top: 58px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        @include below(1320px) {
            margin-bottom: -15px;
        }

        @include below(768px) {
            margin-top: 30px;
        }
    }

    &__item {
        width: 100%;
        max-width: 190px;
        height: 170px;
        background: #FFFFFF;
        border-radius: 10px;
        padding: 30px;

        @include below(1320px) {
            max-width: 32%;
            margin-bottom: 15px;
            height: 140px;
            padding: 15px 30px;
        }

        @include below(768px) {
            max-width: 48%;
            height: 80px;
            display: flex;
            
            align-items: center;
            padding: 20px 0 20px 10px;
        
            &:nth-child(1) {
                order: 1;
            }

            &:nth-child(2) {
                order: 2;
            }

            &:nth-child(3) {
                order: 4;
            }

            &:nth-child(4) {
                order: 3;
            }

            &:nth-child(5) {
                order: 5;
            }

            &:nth-child(6) {
                order: 6;
            }
        }
    }

    &__icon {
        position: relative;
        z-index: 10;
        width: 100%;
        max-width: 44px;
        height: 44px;
        margin-bottom: 16px;
        
        @include below(768px) {
            width: 30px;
            height: 30px;
            margin-right: 10px;
            margin-bottom: 0;
            img {
                min-width: 30px;
                height: 30px;
            }
        }

        &::before,
        &::after {
            content: none;
            position: absolute;
            border-radius: 50%;
            z-index: 2;
            background: rgba(41, 197, 85, 0.1);
        }

        &::before {
            width: 32px;
            height: 32px;

            @include below(768px) {
                width: 24px;
                height: 24px;
            }
        }

        &::after {
            width: 64px;
            height: 64px;
            @include below(768px) {
                width: 45px;
                height: 45px;
            }
        }

        &_1 {
            &::before {
                content: "";
                top: 0;
                left: -10px;
                @include below(768px) {
                    left: -5px;
                }
            }
            &::after {
                content: "";
                bottom: -30px;
                left: -20px;

                @include below(768px) {
                    left: -10px;
                }
            }
        }

        &_3 {
            &::before {
                content: "";
                bottom: -10px;
                left: -10px;
            }

            &::after {
                content: "";
                top: -20px;
                left: -10px;
            }
        }

        &_5 {
            &::before {
                content: "";
                bottom: -10px;
                right: -10px;
            }

            &::after {
                content: "";
                top: -20px;
                left: -15px;

                @include below(768px) {
                    left: -10px;
                }
            }
        }
    }

    &__title {
        @include fontProperty(800, 18px, 110%, $color_dark);

        @include below(768px) {
            font-size: 14px;
            line-height: 110%;
        }
    }
}