@import "main-bx";

.contacts {
    &__inner {
        display: flex;
        margin-top: 30px;

        @include below(1024px) {
            flex-direction: column;
        }
    }

    &__row {
        @include below(768px) {
            display: flex;
            align-items: center;
            .contacts__text {
                width: 40%;
            }
        }

        &_2 {
            @include below(768px) {
                align-items: flex-start;
                flex-direction: column;
                .contacts__text {
                    width: auto;
                }
                &:not(:last-child) {
                    margin-bottom: 30px;
                }
            }
            
        }
    }

    &__left {
        width: 100%;
        max-width: 430px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-right: 40px;
        @include below(1024px) {
            max-width: 100%;
        }
    }

    &__right {
        width: 100%;
        max-width: 730px;
        @include below(1024px) {
            max-width: 100%;
        }
    }

    &__map {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
    }

    &__tel {
        display: inline-block;
        @include fontProperty(800, 24px, 33px, #959CAC);
        margin-bottom: 12px;
        transition: all .2s linear;
        border-bottom: 1px solid rgba(149, 156, 172, 0.3);
        &:hover {
            color: $color_green;
            border-color: $color_green;
        }

        @include below(768px) {
            width: 70%;
            font-size: 20px;
            line-height: 27px;
            border-bottom: 0;
            text-decoration: underline
        }
    }

    &__text {
        @include fontProperty(400, 16px, 22px, $color_dark);
        margin-bottom: 18px;

        a {
            color: $color_green;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__title {
        @include fontProperty(800, 20px, 24px, #959CAC);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        margin-bottom: 20px;
        @include below(768px) {
            font-size: 16px;
            line-height: 20px;  
        }
    }

    &__form {
        background: #F3F4F4;
        border-radius: 10px;
        padding: 30px 30px 15px;

        @include below(768px) {
            padding: 20px;
        }
    }
    
    &__form-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &:not(:last-child) {
            margin-bottom: 12px;
        }
        .form__btn {
            width: 31.5%;
            padding: 16px 0;
            margin: 0;
            @include below(768px) {
                width: 100%;
                margin-top: 12px;
            }
        }

        @include below(768px) {
            flex-direction: column;
        }
        
    }
    
    &__form-input {
        width: 31.5%;

        @include below(768px) {
            width: 100%;
            margin-bottom: 12px;
        }
    }

    &__form-policy {
        @include fontProperty(400, 12px, 140%, rgba(131, 139, 149, 0.5));
        a {
            color: rgba(131, 139, 149, 0.5);
            text-decoration: underline;
        }
    }
}