@import "main-bx";
 

.btn {
    @include fontProperty(800, 14px, 19px, $white);
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border-radius: 10px;
    transition: all .2s linear;
    cursor: pointer;
    display: flex;
    align-items: center;
    position: relative;

    

    &_dark {
        background-color: $color_dark;

        &:hover {
            background-color: #54616A;
            box-shadow: 0px 4px 20px rgba(41, 197, 85, 0.5);
        }

        &:active {
            background-color: #2B2F33;
            box-shadow: none;
        }
    }

    &_link {
        @include fontProperty(400, 18px, 120%, $color_green);
        letter-spacing: normal;
        text-transform: none;
        border-radius: 0;
        span {
            border-bottom: 1px solid transparent;
            transition: all .2s linear;
            &:hover {
                color: #2EEC64;
                border-color: #2EEC64;
            }
    
            &:active {
                color: $color_green; 
                border-color: transparent;
            }
        }
    }

    &_wheat {
        background-color: rgba(255, 255, 255, 0.4);
        &:hover {
            color: #2EEC64;
            background-color: $white;
        }

        &:active {
            color: #26AA4B;
            background-color: $white;
        }
    }


    &_grey {
        background-color: #838B95;
        &:hover {
            background-color: #2EEC64;
        }
        &:active {
            background-color: #26AA4B;
        }
    }

    &_green {
        background-color: $color_green;
        &:hover {
            background-color: #2EEC64;
        }
        &:active {
            background-color: #26AA4B;
        }
    }

    &:disabled {
        span {
            opacity: 0;
        }
        .btn__spin {
            opacity: 1;
        }

        &:hover,
        &:active {
            background-color: #65f38d;
        }
    }

    &__spin {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0 auto;
        opacity: 0;
    } 

    &_map {
        display: flex;
        align-items: center;
        transition: all .2s linear;
        @include fontProperty(800, 16px, 22px, $color_green);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        @include below(1320px) {
            font-size: 12px;
        }

        @include below(768px) {
            font-size: 16px;
        }
        span {
            margin-bottom: -3px;
        }
        &:hover {   
            color: #2EEC64;
            .btn_map-icon {
                background-color: #926539;
                border-color: #926539;
            }
            svg {
                fill: $white;
            }
        }

        &:active {
            color: #29C555;
            .btn_map-icon {
                background-color: #926539;
                border-color: #926539;
            }
            svg {
                fill: $white;
            }
            color: $color_green;
        }
    }

    &_map-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 1.5px solid #29C555;
        margin-right: 12px;
        svg {
            width: 16px;
            height: 16px;
            fill: #926539;
        }
    }
}
