@import "main-bx";

.configurator {
    &__inner {
        margin-top: 60px;
        display: flex;
        justify-content: space-between;
        @include below(768px) {
            flex-direction: column;
            margin-top: 30px;
        }
    }

    &__item {
        width: 45%;
        @include below(768px) {
            width: 100%;
        }
    }

    &__img {
        position: relative;
        margin-bottom: 20px;
        img {
            display: block;
            border-radius: 10px;
        }

        @include below(768px) {
            margin-bottom: 10px;
        }
    }

    &__over {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    &__text {
        padding: 25px;
        border-radius: 10px;
        background-color: rgba(131, 139, 149, .2);
        p {
            @include fontProperty(400, 16px, 140%, $color_dark);
        }

        @include below(768px) {
            padding: 15px;
            font-size: 14px;
            margin-bottom: 30px;
        }
    }

    &__type {
        padding-top: 4px;
        padding-bottom: 30px;
        border-top: 2px solid #EBEBEB;
    }

    &__type-title {
        @include fontProperty(800, 16px, 22px, $color_slates);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        margin-bottom: 20px;

        @include below(768px)  {
            font-size: 14px;
            line-height: 19px;  
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;

        &_justify {
            justify-content: flex-start;
        }

        @include below(1130px) {
            overflow-x: auto;
            overflow-y: hidden;
        }

        &_first {
            @include below(768px) {
                flex-direction: column;
            }
        }
    }

    &__coating {
        display: flex;
        align-items: center;
        width: 50%;
        cursor: pointer;
        &:hover .configurator__coating-circle {
            border-color: $color_green;
            &::before {
                opacity: 0.1;
            }
        }
        &.active .configurator__coating-circle {
            border-color: $color_green;

            &::before {
                opacity: 1;
            }
        }

        span {
            @include fontProperty(400, 16px, 22px, $color_dark);
            letter-spacing: 0.05em;

            @include below(768px) {
                font-size: 14px;
                line-height: 19px;
            }
        }

        @include below(768px) {
            width: 100%;
            &:nth-child(1) {
                margin-bottom: 24px;
            }
        }
    }

    &__coating-circle {
        width: 100%;
        max-width: 24px;
        height: 24px;
        border-radius: 50%;
        border: 1px solid #C1C5CA;
        margin-right: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        &::before {
            content: "";
            z-index: 10;
            width: 100%;
            max-width: 12px;
            height: 12px;
            background-color: $color_green;
            border: 1px solid #0F9F37;
            border-radius: 50%;
            opacity: 0;
            transition: all .2s linear;
        }
    }

    &__timber {
        cursor: pointer;
        width: 22%;

        @include below(1130px) {
            width: 100%;
            min-width: 100px;
            margin-right: 12px;
        }
        &:hover {
            .configurator__timber-img {
                border-color: #29C555;
            }
            .configurator__timber-name {
                color: #29C555;
            }
        }
        &.active {
            .configurator__timber-img {
                border-color: #0F9F37;
            }
            .configurator__timber-check {
                opacity: 1;
            }
            .configurator__timber-name {
                color: #29C555;
            }
        }
    }

    &__timber-img {
        position: relative;
        border: 2px solid transparent;
        transition: all .2s linear;
        border-radius: 7px;
        img {
            display: block;
            // border-radius: 7px;    
        }

    }

    

    &__timber-check {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 0;
        bottom: 0;
        opacity: 0;
        width: 30px;
        height: 30px;
        border-top-right-radius: 5px;
        border-bottom-left-radius: 5px;
        background-color: $color_green;
        transition: all .2s linear;

        img {
            width: 22px;
            height: 22px;
        }
    }

    &__timber-name {
        margin-top: 10px;
        @include fontProperty(600, 14px, 100%, $color_dark);
        letter-spacing: 0.05em;
    }

    &__color {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        max-width: 56px;
        min-width: 56px;
        height: 56px;
        border-radius: 50%;
        border: 1px solid transparent;
        &_1:not(:last-child) {
            margin-right: 13px;
        }
        &_2:not(:last-child) {
            margin-right: 5px;
        }
        cursor: pointer;
        &:hover {
            border-color: rgba(41, 197, 85, 0.5);
        }
        &.active {
            border-color: $color_green;
        }
        img {
            width: 44px;
            height: 44px;
            display: block;
            border-radius: 50%;
        }
    }

    &__send {
        margin-top: 10px;
        padding: 15px 45px;
        filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.16));
        @include below(768px) {
            display: none;
            width: 100%;
        }
        &_mob {
            display: none;
            @include below(768px) {
                display: block;
            }
        }
        
    }
}