@import "main-bx";

.footer {
    &_bg {
        background-color: $color_dark;
    }
    &__inner {
        padding-bottom: 10px;
    }

    &__logo {
        width: 100%;
        max-width: 165px;
        height: 32px;

        @include below(768px) {
            max-width: 134px;
            height: 26px;
        }

        @include below(360px) {
            max-width: 120px;
            height: 20px;
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 40px 0;
        @include below(768px) {
            padding: 20px 0;
        }
        &:first-child {
            border-bottom: 1px solid rgba(250, 250, 250, .2);
        }

        &_last {
            @include below(768px) {
                flex-direction: column-reverse;
                align-items: center
            }
        }
    }

    &__tel {
        @include fontProperty(800, 24px, 33px, $white);
        transition: all .2s linear;
        &:hover {
            color: $color_green;
        }

        @include below(768px) {
            font-size: 18px;
            line-height: 25px;
        }

        @include below(360px) {
            font-size: 14px;
        }
    }

    &__copy {
        @include fontProperty(400, 14px, 100%, $color_slates);
    }

    &__social {
        display: flex;
        align-items: center;
        @include below(768px) {
            margin-bottom: 30px;
        }
        a {
            display: block;
            width: 24px;
            height: 24px;
            &:not(:last-child) {
                margin-right: 24px;
            }

            &:hover {
                svg {
                    fill: $color_green;
                }
            }
        }

        svg {
            width: 24px;
            height: 24px;
            fill: #838B95;
            transition: all .2s linear;
        }
    }
}