@import "main-bx";

.features {
    &_bg {
        background-color: #926539;
    }

    &__inner {
        padding: 50px 0;
        display: flex;
        justify-content: space-between;

        @include below(1130px) {
            flex-direction: column;
        }
    }

    &__left {
        width: 100%;
        max-width: 350px;
        padding-right: 40px;
        padding-top: 5px;
        padding-bottom: 5px;
        border-right: 1px solid $white;
        margin-right: 35px;

        @include below(1130px) {
            order: 1;
            border-right: 0;
            margin-top: 20px;
            padding-top: 20px;
            padding-bottom: 0;
            margin-right: 0;
            padding-right: 0;
            max-width: 500px;
            border-top: 1px solid $white;
        }

        @include below(768px)  {
            padding: 0;
            margin: 0 0 32px;
            order: -1;
            border-top: 0;
            border-bottom: 1px solid $white;
        }
    }

    &__text {
        @include fontProperty(600, 18px, 130%, $white);
        margin-bottom: 24px;

        @include below(765px) {
            font-size: 14px;
            line-height: 130%;
        }

        @include below(768px) {
            margin-bottom: 20px;
        }
    }

    &__btn {
        padding: 15px 30px;

        @include below(768px) {
            display: none;
        }
        &_mobile {
            display: none;

            @include below(768px) {
                display: inline-block;
            }
        }
    }

    &__right {
        padding: 5px 0;
        display: flex;
        justify-content: space-between;

        @include below(765px) {
            flex-wrap: wrap;
        }
    }

    &__col {
        width: 30%;
        height: 100%;
        @include below(765px) {
            width: 100%;
        }
    }

    &__item {
        @include below(768px) {
            margin-bottom: 30px;
        }

        &:not(:last-child) {
            margin-bottom: 48px;
            @include below(765px) {
                margin-bottom: 0;
            }
        }
    }

    &__item-title {
        @include fontProperty(700, 14px, 100%, $white);
        letter-spacing: 0.05em;
        text-transform: uppercase;
        margin-bottom: 12px;
    }
    &__item-text {
        @include fontProperty(400, 16px, 140%, $white);
    }
}