@import "main-bx";

.header {

    &__inner {
        padding-top: 40px;
        padding-bottom: 40px;

        display: flex;
        justify-content: space-between;
        align-items: center;
        @include below(1124px) {
            padding-top: 25px;
            padding-bottom: 25px;
        }

        @include below(768px) {
            padding-top: 18px;
            padding-bottom: 18px;
        }
    }

    &__right {
        @include below(768px) {
            display: flex;
            align-items: center;
        }
    }

	&__logo {
        width: 100%;
        max-width: 257px;
        height: 50px;
        display: block;

        @include below(768px) {
            max-width: 130px;
            height: 26px;
        }

        @include below(360px) {
            max-width: 120px;
            height: 20px;
        }
    }

    &__btn {
        min-width: 32px;
        height: 32px;
        display: none;
        align-items: center;
        justify-content: center;
        background-color: #29C555;
        border-radius: 30px;
        margin-left: 16px;

        &.active {
            background-color: $color_dark;
            .header__btn-icon1 {
                display: none;
            }
            .header__btn-icon2 {
                display: block;
            }
        } 

        @include below(768px) {
            display: flex;
        }
        img {
            width: 20px;
            height: 20px;
        }
    }

    &__btn-icon2 {
        display: none;
    }

    &__contacts {

        a {
            @include fontProperty(800, 20px, 27px, $color_dark);
            transition: all .2s linear;
            &:hover {
                color: $color_green;
            }

            @include below(768px) {
                font-size: 14px;
                border-bottom: 1px solid rgba(56, 63, 68, 0.3);
            }
            @include below(360px) {
                display: none;
            }
        }

        &-sub {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            width: 100%;
            font-weight: normal;
            font-size: 14px;
            line-height: 19px;

            cursor: pointer;
            transition: all .2s linear;
            &:hover {
                color: $color_green;
            }
            &::after {
                content: "";
                display: block;
                background-color: $color_green;
                border-radius: 50%;
                width: 8px;
                height: 8px;
                margin-left: 10px;
            }

            @include below(768px) {
                display: none;
            }
        }
    }

    &__menu {
        background-color: $color_green;

        @include below(768px) {
            background-color: $white;
            position: absolute;
            top: 68px;
            left: 0;
            right: 0;
            z-index: 100;
            max-width: 375px;
            transform: translateX(-200%);
            transition: all .3s linear;
        }
        &.active {
            @include below(768px) {
                transform: translateX(0);
            }
        }
        .menu__list {
            @include below(768px) {
                flex-direction: column;
                padding: 25px 15px;
            }
        }
        .menu__list-item {
            @include below(768px) {
                margin-right: 0;
            }
        }
        .menu__list-item a {
            @include below(768px) {
                color: $color_dark;
                text-align: center;
                font-size: 18px;
                line-height: 25px;
                &:hover {
                    color: #29C555;
                }
            }
        }
    }

    &__hidden {
        display: none;
        padding: 0 15px;
        @include below(768px) {
            display: block;
        }
    }

    &__hidden-inner {
        border-top: 1px solid #E0E2E7;
        padding: 36px 25px 60px;
        display: flex;
        flex-direction: column;
        align-items: center;

    }
    &__hidden-tel {
        @include fontProperty(800, 20px, 27px, #959CAC);
        border-bottom: 1px solid #959CAC;
        text-align: center;
    }

    &__hidden-text {
        @include fontProperty(400, 14px, 19px, $color_dark);
        margin-top: 10px;
        text-align: center;
        &:nth-child(1) {
            margin-bottom: 34px;
        }
    }
    &__map {
        padding-top: 16px;
    }
}