@import "main-bx";

.products {
    &__inner {
        padding-top: 60px;
        padding-bottom: 80px;

        display: flex;
        justify-content: space-between;

        @include below(1320px) {
            padding-bottom: 60px;
        }

        @include below(768px) {
            flex-wrap: wrap;
            padding-top: 30px;
            padding-bottom: 40px;
        }
    }

    &__item {
        width: 30%;
        border: 1px solid #F3F4F4;
        background: $white;
        border-radius: 10px;

        @include below(768px) {
            width: 100%;

            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }
    }

    &__body {
        height: 188px;
        padding: 16px 26px 0;

        @include below(768px) {
            height: auto;
            padding: 16px 26px 26px;
        }
    }

    &__title {
        @include fontProperty(800, 20px, 130%, $color_dark);
        margin-bottom: 20px;

        @include below(1024px) {
           margin-bottom: 15px;
        }
    }

    &__text {
        @include fontProperty(400, 16px, 130%, $color_dark);

        @include below(1024px) {
            font-size: 14px;
            line-height: 130%;
        }
    }
}