@import "main-bx";

.section {
    
    &_second {
        background-color: #FAFBFB;
        padding: 60px 0 52px;

        @include below(768px) {
            padding: 40px 0 40px;
        }
    }

    &_third {
        padding-top: 78px;
        overflow: hidden;
        @include below(1320px) {
            padding-top: 60px;
        }

        @include below(768px) {
            padding-top: 40px;
            margin-bottom: 40px;
        }
    }

    &_fourth {
        padding-top: 100px;
        padding-bottom: 110px;

        @include below(1320px) {
            padding-top: 60px;
            padding-bottom: 60px;
        }
        @include below(768px) {
            padding-top: 40px;
            padding-bottom: 40px;
        }
    }

    &_fifth {
        padding: 80px 0 87px;
        @include below(1320px) {
            padding-top: 60px;
            padding-bottom: 60px;
        }

        @include below(768px) {
            padding-top: 40px;
            padding-bottom: 40px;
        }
    }

    &_sixth {
        padding: 113px 0 80px;
        @include below(1320px) {
            padding-top: 60px;
            padding-bottom: 60px;
        }
        @include below(768px) {
            padding: 40px 0 100px;
        }
    }

    &_seventh {
        padding: 60px 0 110px;
        border-top: 1px solid #F3F4F4;
        border-bottom: 1px solid #F3F4F4;
        @include below(1320px) {
            padding-bottom: 60px;
        }

        @include below(768px) {
            padding: 30px 0 60px;
        }
    }

    &_ninth {
        padding: 60px 0 80px;

        @include below(1320px) {
            padding: 50px 0 60px;
        }

    }

    &__title {
        @include fontProperty(800, 36px, 100%, $color_dark);
        text-align: center;

        &_left {
            text-align: left;
            @include below(768px) {
                text-align: center;
            }
        }

        @include below(1320px) {
            font-size: 30px;
        }
        @include below(768px) {
            font-size: 24px;
        }

        &_left1 {
            text-align: left;
        }

        &_24 {
            font-size: 24px;
            line-height: 29px;
            @include below(768px) {
                font-size: 18px;
                line-height: 110%;
            }
            
        }
    }

    &__text {
        @include fontProperty(400, 18px, 25px, $color_dark);
        text-align: center;
        @include below(768px) {
            font-size: 14px;
            line-height: 19px;
        }
        &_mt {
            margin-top: 37px;
            @include below(768px) {
                margin-top: 25px;
            }
        }
    }

}