@import "main-bx";

.our-work {
    @include below(1110px) {
        overflow: hidden;
    }
    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 54px;

        @include below(1000px) {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 30px;
        }

        h2 {
            @include below(1000px) {
                margin-bottom: 20px;
            }
        }
    }

    &__tabs {
        background: #FAFBFB;
        border: 1px solid #EBEBEB;
        border-radius: 10px;
        @include below(1000px) {
            border: 0;
            display: flex;
            flex-wrap: wrap;
            margin: -12px 0 0 -12px;
            background: transparent;
        }
    }

    &__tabs-btn {
        position: relative;
        font-weight: 800;
        font-size: 14px;
        line-height: 100%;
        padding: 15px 30px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: rgba(149, 156, 172, 0.6);
        transition: all .2s linear;
        span {
            position: relative;
            z-index: 100;
        }
        &.active {
            color: $white;
            &::before {
                opacity: 1;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: -2.5px;
            left: 0;
            z-index: 1;
            width: 100%;
            height: 50px;
            background: #383F44;
            border-radius: 10px;
            opacity: 0;
            transition: all .2s linear;
            @include below(1000px) {
                height: 40px;
            }
        }

        @include below(1000px) {
            margin: 12px 0 0 12px;
            border: 1px solid #EBEBEB;
            border-radius: 10px;
            padding: 11px 14px;
        }
    }

    &__slider {
        .slick-track {
            margin: 0 !important;
        }
        .slick-list {
            margin: 0 -35px;
        }

        .slick-arrow {
            width: 50px;
            height: 50px;
            background: #FFFFFF;
            border: 1px solid rgba(131, 139, 149, 0.5);
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            z-index: 1000;
            &::before {
                content: "";
            }

            &:hover {
                filter: drop-shadow(0px 6px 30px rgba(56, 63, 68, 0.1));
                border: 1px solid rgba(131, 139, 149, 0.2);
                background: #FFFFFF;
                svg {
                    fill: #29C555;
                }
            }
            &:active {
                background: #FFFFFF;
                filter: 0;
                border: 1px solid rgba(131, 139, 149, 0.5);
            }
            svg {
                width: 24px;
                height: 24px;
                fill: #383F44;
            }

            @include below(769px) {
                top: auto;
                bottom: -100px;
            }
        }

        .slick-prev {
            left: -70px;
            @include below(1230px){
                left: 0;
            } 
        }

        .slick-next {
            right: -60px;
            @include below(1230px){
                right: 0;
            } 

            @include below(769px) {
                right: auto;
                left: 70px;
            }
        }


        .slick-arrow.slick-disabled {
            opacity: 1;
        }

        .slick-dots {
            text-align: right;
            bottom: -62px;
            li {
                width: 16px;
                height: 16px;
                border-radius: 50%;
                background: #E2E5E7;
                &.slick-active {
                    background-color: $color_green;
                }
            }

            button {
                display: none;
            }
        }
    }

    &__slider-item {
        width: 100%;
        height: 470px;
        border-radius: 10px;
        overflow: hidden;
        border: 1px solid #F3F4F4;

        &.slick-slide {
            height: 470px;
            margin: 0 35px;
        }
    }

    &__slider-img {
        position: relative;
        height: 230px;
    }

    &__category {
        position: absolute;
        top: 0;
        left: 0;
        font-weight: 800;
        font-size: 16px;
        line-height: 100%;
        text-transform: uppercase;
        color: #FFFFFF;
        padding: 9px 12px;
        border-radius: 0px 0px 10px 0px;
        box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15), 0px 6px 10px rgba(0, 0, 0, 0.2);
        &_orange {
            background: #FFA404;
        }
        &_blue {
            background-color: #26B1FF;
        }
        &_green {
            background-color: #29C555;
        }

        &_yelow {
            background-color: #ceb31e;
        }
    }

    &__slider-body {
        padding: 16px 26px;
    }

    &__slider-title {
        @include fontProperty(800, 20px, 130%, $color_dark);
        margin-bottom: 16px;
    }

    &__slider-text {
        @include fontProperty(400, 16px, 140%, $color_dark);
    }

    &__empty {
        @include fontProperty(600, 25px, 16px, $color_dark);
        display: none;
    }
}