@import "main-bx";

.form {

    fieldset {
        padding: 0;
        margin: 0;
        border: 0;
    }

    &__input {
        width: 100%;
        font-size: 14px;
        border-radius: 10px;
        background-color: $white ;
        border: 1px solid #EBEBEB;
        padding: 16px 18px;
        transition: all .2s linear;
        resize: none;
        &::placeholder {
            color: rgba(131, 139, 149, 0.7);
        }

        &:hover {
            border-color: #838B95;
        }

        &:focus {
            border-color: #29C555;
        }

        &.error {
            border-color: rgb(233, 18, 18);
            &::placeholder {
                color: rgb(233, 18, 18);
            }
        }

        &.disabled {
            opacity: 0.6;
        }
    }

    &__file {
        width: 100%; 
        display: block;
        padding: 16px 0;
        background: rgba(255, 255, 255, 0.1);
        border: 1px dashed #C1C5CA;
        border-radius: 10px;
        cursor: pointer;
        &:hover {
            border-color: #838B95;
        }

        &:focus {
            border-color: #29C555;
        }
    }

    &__file-content {
        font-size: 18px;
        line-height: 120%;
        color: $white;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            width: 24px;
            height: 24px;
            margin-right: 5px;
        }
    }

    &__btn {
        width: 100%;
        padding: 16px;
        display: block;
        text-align: center;
        margin-top: 20px;
    }
} 