@import "main-bx";

.working {
    background-color: #FAFBFB;
    overflow: hidden;
    &__inner {
        position: relative;
        padding-top: 70px;
        padding-bottom: 60px;

        @include below(768px) {
            padding-top: 30px;
            padding-bottom: 40px;
        }
    }

    &__d-flex {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &:not(:last-child) {
            margin-bottom: 60px;
        }

        @include below(1130px) {
            
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__content {
        width: 100%;
        max-width: 285px;
        margin-right: 23px;

        @include below(1130px) {
            margin-bottom: 100px;
        }

        @include below(768px) {
            margin-bottom: 0;
        }
    }

    &__container {
        display: none;
        &.active {
            display: block;
        } 
    }

    &__container-inner {
        display: flex;
        justify-content: space-between;

        .slick-list {
            padding-top: 50px;
        }

        .slick-arrow {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: $white !important;
            border: 1px solid rgba(131, 139, 149, 0.5);
            display: flex;
            align-items: center;
            justify-content: center;
            right: -55px;
            &::before {
                content: none;
            }
            svg {
                width: 25px;
                height: 25px;
                fill: #383F44;
            }

            &:hover {
                filter: drop-shadow(0px 6px 30px rgba(56, 63, 68, 0.1));
                border: 1px solid rgba(131, 139, 149, 0.2);
                svg {
                    fill: #29C555;
                }
            }
            &:active {
                filter: 0;
                border: 1px solid rgba(131, 139, 149, 0.5);
            }
        }

        .slick-next {
            top: auto;
            right: auto;
            left: 70px;
            bottom: -100px;
        }
        .slick-prev {
            top: auto;
            bottom: -100px;
            left: 0;
        }
    }

    &__steps-btn {
        position: absolute;
        top: 45%;
        right: -80px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: $white !important;
        border: 1px solid rgba(131, 139, 149, 0.5);
        display: flex;
        align-items: center;
        justify-content: center;
        svg {
            width: 25px;
            height: 25px;
            fill: #383F44;
        }

        @include below(768px) {
            display: none;
        }
    }

    &__steps {
        position: relative;
        width: 100%;
        max-width: 890px;

        @include below(1130px) {
            padding-top: 40px;
        }
        
    }
    &__steps-item {
        width: 100%;
        max-width: 280px;
        min-height: 290px;
        border-radius: 10px;
        background-color: $white;
        padding: 26px;
        position: relative;
        margin-right: 26px;

        @include below(768px) {
            padding-bottom: 70px;
            max-width: max-content;
        }
        &.slick-slide {
            min-height: 290px;

            @include below(768px) {
                min-height: 1px;
            }
        }
        &_4 {
            max-width: 586px;
        }

        &_11 {
            display: flex;
            flex-direction: column;
            justify-content: center;

            img {
                width: 24px;
                height: 24px;
            }
        }
    }

    &__steps-row {
        display: flex;
        align-items: center;
        padding: 15px 0;
        span {
            @include fontProperty(800, 20px, 27px, $color_dark);
        }
    }

    &__steps-icon {
        width: 100%;
        max-width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #F3F4F4;
        border-radius: 50%;
        margin-right: 10px;

        img {
            width: 26px;
            height: 26px;
        }
    }

    &__steps-num {
        position: absolute;
        top: -22px;
        left: 4px;
        display: flex;
        align-items: center;
        justify-content: center;
        @include fontProperty(800, 16px, 22px, $white);
        width: 44px;
        height: 44px;
        background: radial-gradient(50% 50% at 50% 50%, rgba(41, 197, 85, 0.9) 46.69%, rgba(41, 197, 85, 0) 100%);
    }

    &__steps-title {
        @include fontProperty(800, 16px, 22px, $color_dark);
        margin-bottom: 16px;
    }

    &__steps-text {
        @include fontProperty(400, 16px, 22px, rgba(56, 63, 68, 0.8));
        a {
            color: $color_green;
        }
    }

    &__info-text {
        width: 100%;
        position: absolute;
        bottom: 70px;
        z-index: 1100;
        background: #383F44;
        box-shadow: 0px 6px 20px rgba(56, 63, 68, 0.1);
        border-radius: 10px;
        display: none;

        &_3 {
            min-width: 330px;
            left: -50px;

            @include below(768px) {
                min-width: 300px;
                left: 0;
                bottom: 100px;
            }
        }

        &_9 {
            min-width: 450px;
            left: -100px;

            @include below(768px) {
                min-width: 300px;
                left: 0;
                bottom: 100px;
                height: 200px;
                overflow-y: auto;
            }
        }

        p {
            @include fontProperty(400, 12px, 16px, $white);
        }
    }

    &__info-block {
        position: relative;
        padding: 15px;

        &::before {
            content: "";
            position: absolute;
            bottom: -8px;
            left: 75px;
            width: 20px;
            height: 20px;
            background-color: #383F44;
            transform: rotate(45deg);
        }

        &_9 {
            &::before {
                left: 125px;
            }
        }
    }

    &__info-close {
        position: absolute;
        top: 5px;
        right: 5px;
        width: 18px;
        height: 18px;
        cursor: pointer;
    }

    &__info {
        display: flex;
        align-items: center;
        position: absolute;
        bottom: 26px;
        left: 26px;
        cursor: pointer;

        &:hover {
            opacity: .7;
        }
        img {
            width: 19px;
            height: 19px;
            margin-right: 4px;
        }

        span {
            display: block;
            @include fontProperty(700, 14px, 19px, #926539);
            margin-bottom: -2.5px;
        }

        @include below(768px) {
            bottom: auto;
            left: auto;
            position: relative;
            margin-top: 30px;
        }
    }

    &__title {
        margin-bottom: 68px;
        @include below(1130px) {
            margin-bottom: 25px;
        }

        @include below(768px) {
            text-align: left;
        }
    }
    

    &__step {
        @include fontProperty(400, 32px, 100%, #838B95);
        text-transform: uppercase;
        opacity: 0.4;

        @include below(768px) {
            display: none;
        }
    }

    &__tab {
        display: flex;
        align-items: center;
        width: 100%;
        max-width: 890px;
        overflow: hidden;
        border-radius: 5px;

        @include below(1130px) {
            margin-top: 20px;
        }

        @include below(768px) {
            position: absolute;
            top: 100px;
            left: 15px;
        }

        @include below(374px) {
            left: 0;
        }
    }

    &__tab-item {
        position: relative;
        @include fontProperty(800, 16px, 100%, rgba(131, 139, 149, 0.6));
        text-transform: uppercase;
        width: 100%;
        max-width: 230px;
        text-align: center;
        padding: 10px 0;
        background: rgba(131, 139, 149, 0.2);
        cursor: pointer;
        transition: all .2s linear;
        @include below(768px) {
            border-radius: 5px;
            font-size: 14px;
            line-height: 19px;
            max-width: 86px;
        }
        &::before {
            content: "";
            display: block;
            width: 40px;
            height: 40px;
            position: absolute;
            top: -3px;
            left: -25px;
            z-index: 1;
            border-top: 15px solid #e2e5e7;
            border-right: 15px solid #e2e5e7;
            transform: rotate(45deg);
            @include below(768px) {
                content: none;
            }
        }

        &:not(:last-child) {
           
            &::after {
                content: "";
                display: block;
                width: 30px;
                height: 30px;
                position: absolute;
                top: 3px;
                right: 0;
                z-index: 2;
                border-top: 7px solid #fff;
                border-right: 7px solid #fff;
                transform: rotate(45deg);
                @include below(768px) {
                    content: none;
                }
            }
        }

        &.active {
            background-color: $color_green;
            color: $white;

            &::before {
                content: none;
            }

            &.after-active {
                color: #229342;
            }
        }
    }

    
}