@import "main-bx";

.payment-form {
    &__inner {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 70px;

        @include below(768px) {
            margin-top: 40px;
        }
    }

    &__item {
        width: 27%;

        @include below(768px) {
            width: 100%;
            &:not(:last-child) {
                margin-bottom: 40px;
            }
        }
    }

    &__icon {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #926539;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto 31px;
        img {
            max-width: 50px;
            height: auto;
        }
    }

    &__text {
        font-weight: normal;
        font-size: 16px;
        line-height: 140%;
        color: $color_dark;
        text-align: center;
    }
}